import React from 'react';

export default class All_markers extends React.Component {

    popap = (id) => {
        this.props.popap(id)
    }

    render() {
        return (
                <div className="marker" onClick={()=> this.popap(this.props.id)}>
                    {this.props.markers}
                </div>
        )
    }
}



