import React from 'react';

export default class Create_new extends React.Component {

    create = (e) => {
        e.preventDefault();
        if(this.shir.value === '' || this.dolg.value === '' || this.text.value === '') {
            return
        }
        else {
            let new_mass = {
                point: [Number(this.shir.value), Number(this.dolg.value)],
                text:  this.text.value
            };
            this.props.create(new_mass);
            this.shir.value = ''; this.dolg.value = ''; this.text.value = '';
        }
    }

    render() {
        return (
            <form className="create_new">
                <h5></h5>
                <input type="number" placeholder='55.744665' ref={input => this.shir = input} required />
                <h5></h5>
                <input type="number" placeholder='37.491304' ref={input => this.dolg = input} required />
                <h5></h5>
                <input type="text" placeholder='' ref={input => this.text = input} required />
                <button onClick={this.create}></button>
            </form>
        )
    }
}



