import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Portal from './components/portal';
import AllMarkers from './components/all_markers';
import CreateNew from './components/create_new';
import { Map, Marker } from 'yandex-map-react';

class App extends React.Component {

    state = {
        markers: [
            {point: [55.744665, 37.491304], text: '- 1', zoom:15, id:0},
            {point: [55.636063, 37.566835], text: '- 2', zoom:15.1, id:1},
            {point: [55.865323, 37.599794], text: '- 3', zoom:15.2, id:2}
        ],
        show: false,
        filtered: {point: [55.754734, 37.583314], zoom:12, id:2}
    }

    create = (elem) => {
        elem.zoom = '15.' + this.state.markers.length;
        elem.id = this.state.markers.length;
        let add = this.state.markers;
        add.push(elem)
        this.setState({
            markers: add
        })
    }

    close = () => {
        this.setState({
            show: false
        })
    }

    popap = (item) => {
        this.setState({
            show: true,
            filtered: this.state.markers[item]
        })
    }

    render() {
        return (
            <div>
                <div className="all_markers">
                {this.state.markers.map((item, id) => (
                    <AllMarkers markers={this.state.markers[id].text} id={this.state.markers[id].id} popap={this.popap} key={this.state.markers[id].id}/>
                ))}
                </div>
            <Map onAPIAvailable={function () { console.log('API loaded'); }} center={this.state.filtered.point} width={`100%`} height={`100vh`} zoom={this.state.filtered.zoom}>
                {this.state.markers.map((item, id) => (
                    <Marker lat={item.point[0]} lon={item.point[1]} key={item.id} onClick={() => this.popap(item.id)} />
                ))}
            </Map>
            <CreateNew create={this.create} />
            {this.state.show ? ReactDOM.createPortal(<Portal text={this.state.filtered.text} close={this.close}/>, document.querySelector('#root')) : null}
        </div>
        )
    }
}

ReactDOM.render(<App/>, document.getElementById('map'));



