import React from 'react';

export default class Portal extends React.Component {

    close = () => {
        this.props.close()
    }

    render() {
        return (
            <div className="portal">
                <div className="close" onClick={this.close}>&#215;</div>
                <h2>{this.props.text}</h2>
            </div>
        )
    }
}



